.header {
  display: flex;
  justify-content: space-between;
  padding: 0 30px;
  align-items: center;
  background-color: #f0f2f5;
  border-bottom: 1px solid #d9d9d9;
}

.input-switch + label {
  position: relative;
  display: inline-block;
  height: 25px;
  width: 75px;
  border: 1px solid #000;
  border-radius: 5px;
  background-color: #666;
  line-height: 25px;
  text-align: left;
  transition: background-color 0.3s;
}

.input-switch {
  display: none;
}

.input-switch + label span.grip {
  position: absolute;
  top: 0px;
  left: 0px;
  display: inline-block;
  width: 15px;
  height: 25px;
  background-color: #EEE;
  border-radius: 5px;
  transition: left 0.5s;
}

.input-switch + label span.switch-label {
  position: absolute;
  display: inline-block;
  top: 0px;
  left: 17px;
  width: 56px;
  overflow: hidden;
  text-align: center;
  transition: opacity 0.5s;
}

.input-switch + label span.switch-label.on {
  left: 2px;
  opacity: 0;
}

.input-switch:checked + label {
  background-color: #39F;
}

.input-switch:checked + label span.grip {
  left: 60px;
}

.input-switch:checked + label span.switch-label {
  opacity: 0;
}

.input-switch:checked + label span.switch-label.on {
  opacity: 1;
}